/* Position and sizing of burger button */
.bm-burger-button {
    display: none;
    position: relative;
    width: 36px;
    height: 24px;
    top: 0;
  }

  .sidebar-brand{
      padding: 15px;
      margin-top: 15px;
      margin-bottom: 15px;
  }
  
  
/* Color/shape of burger icon bars */
 .bm-burger-bars {
    background: #ffffff;
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background: #a90000;
  }
  
  /* Position and sizing of clickable cross button */
   .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
   .bm-cross {
    background: #bdc3c7;
  }
  
  /*
    Sidebar wrapper styles
    Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
    */
   .bm-menu-wrap {
    top: 0 !important;
    left: 0px !important;
    position: fixed;
    height: 100vh;
    width: 100% !important;
  }
  
  /* General sidebar styles */
   .bm-menu {
    background: #572bd9;
    padding: 0;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #373a47;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #ffffff;
    padding: 0;
  }
  
  /* Individual item */
  .bm-item {
    display: inline-block;
  }
  
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(0, 0, 0, 0.3);
    left: 0px !important;
    top: 0px !important;
  }

  .side-navbar-body {
    padding: 10px 0px 20px 0px;
    overflow: hidden !important;
  }
  .sidenav-items:first-child {
    border-top: 1px solid #dfdfdf;
  }
  .sidenav-items {
    display: block;
    color: rgb(255, 255, 255);
    border-bottom: 1px solid #dfdfdf;
    text-decoration: none;
    margin-left: 1em;
    margin-right: 1em;
    padding-top: 1em;
    padding-bottom: 1em;
    font: normal 18px "segoeui";
  }


  @media (max-width: 767px) {
    .bm-burger-button {
     display: block;
   }
 }