@font-face {
  font-family: "segoeui";
  src: local("segoeui"), url("./assets/fonts/SEGOEUI.TTF") format("truetype");
}

body {
  margin: 0;
  font-family: "segoeui", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  height: 10vh;
  width: 100%;
  padding-left: 60px;
  padding-right: 60px;
  position: fixed;
  z-index: 100000;
}

.navbar.active {
  background-color: #4f29bf !important;
}

.navbar-brand {
  color: #ffffff !important;
}

.nav-item {
  margin-right: 10px !important;
}

.nav-link {
  color: #f0f4fa !important;
  font-size: 16px;
  font-weight: 700;
  font-family: "Open Sans", sans-serif;
}

.header {
  font-size: 32px;
  font-weight: 700;
  color: #ffffff;
}

.special-btn-2 {
  background: #ffdd33;
  color: #ffffff;
  margin-right: 10px;
  transition: all 1s ease-in-out;
}
.special-btn-2:hover {
  background: #c9ab14;
  color: #ffffff;
}

.special-btn {
  background: #572bd9;
  color: #ffffff;
  transition: all 1s ease-in-out;
}
.special-btn:hover {
  background: #4c25c0;
  color: #dddddd;
}

#section-1 {
  background-color: #4f29bf;
  position: relative;
}

#section-1 .inner-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-left: 200px;
  padding-right: 200px;
  padding-top: 10vh;
}

#section-1 .banner {
  width: 50%;
  height: 250px;
  background-position: center !important;
  background-size: cover !important;
  background-image: url("./assets/img/apple-606761_1280.jpg") !important;
  border-radius: 10px;
  margin-bottom: 40px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 40px;
}

#section-1 .inner-container .special-btn {
  margin-right: 12px;
}

#section-1 .inner-container h4 {
  margin-bottom: 20px;
}

#section-1 .inner-container h6 {
  font-size: 20px;
  color: #ffffff;
  margin-bottom: 20px;
  text-align: center;
}

/* section 2 */

#section-2 {
  height: 200px;
  background-color: rgb(255, 255, 255);
  width: 100%;
}

#section-2 img {
  height: 200px;
  width: 100%;
  padding-left: 50px;
  padding-right: 50px;
}

#section-3 {
  background: #fbfcfe;
  padding-top: 60px;
  padding-bottom: 60px;
}

#section-3 h4 {
  text-align: center;
  color: #040404;
  font-weight: 700;
  margin-bottom: 28px;
}

#section-3 h6 {
  text-align: center;
  color: #7e92ac;
}

#section-3 #inner-container {
  display: flex;
  justify-content: space-evenly;
  margin-top: 30px;
  margin-left: 70px;
  margin-right: 70px;
}

#section-3 .card {
  width: 280px;
  background: rgb(255, 255, 255);
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 30px;
  padding-bottom: 30px;
  margin-right: 10px;
  transition: all 1s ease-in-out;
  /*   height: 70px; */
}

#section-3 .card:hover {
  background: #fafafa;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.07);
  /*   height: 70px; */
}

#section-3 .card img {
  width: 73px;
  height: 70px;
  /*   height: 70px; */
}

#section-3 h5 {
  margin-top: 1.3rem !important;
  margin-bottom: 1.3rem !important;
  font-size: 20px;
  font-weight: 700;
  text-align: left;
  /*   height: 70px; */
}

#section-3 p {
  text-align: left;
  color: #7e92ac;
  font-size: 16px;
  /*   height: 70px; */
}

#section-4 {
  background: #ffffff;
  /*   height: 380px; */
  padding: 40px 40px;
}

#section-4 .row {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

#section-4 #inner-banner {
  width: 100%;
  height: 300px;
  background-position: center !important;
  background-size: cover !important;
  background-image: url("./assets/img/tech-5090539_1280.jpg") !important;
  border-radius: 20px;
}

#section-4 #inner-container {
  background-color: rgb(255, 255, 255);
  padding-top: 30px;
  padding-bottom: 30px;
}

#section-4 h4 {
  color: #040404;
}

#section-4 p {
  color: #7e92ac;
  font-size: 16px;
  margin-bottom: 40px;
}

#section-5 {
  width: 100%;
  height: 400px;
  background-color: #5b2aee;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-left: 40px;
  padding-right: 40px;
  /*   background-position: center !important;
  background-size: cover !important;
  background-image: url("./assets/img/banner2.svg") !important;
  border-radius: 0px; */
}

#section-5 p {
  text-align: center;
}

#section-5 h4 {
  color: #ffffff;
  margin-bottom: 28px;
  font-family: "segoeui", sans-serif;
}

#section-5 p {
  font-size: 16px;
  color: #ffffff;
  margin-bottom: 28px;
  font-family: "segoeui", sans-serif;
}

#section-6 {
  background: #ffffff;
  /*   height: 380px; */
  padding: 40px 40px;
}

#section-6 .title {
  color: #040404;
  margin-bottom: 22px;
}
#section-6 .header {
  color: #000000;
  margin-bottom: 22px;
}

#section-6 .row {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

#section-6 #inner-banner {
  width: 100%;
  height: 350px;
  background-position: center !important;
  background-size: cover !important;
  background-image: url("./assets/img/earth-2254769_1280.jpg") !important;
  border-radius: 20px;
}

#section-6 .inner-container {
  display: flex;
}

#section-6 .inner-container:not(:last-child) {
  margin-bottom: 12px;
}

#section-6 .inner-container img {
  margin-right: 12px;
}

#section-6 .inner-container p {
  color: #7e92ac;
  font-size: 16px;
}

#section-6 button {
  margin-top: 22px;
}

#section-7 {
  padding: 40px 0px;
}

#section-7 .header {
  color: #000000;
  text-align: center;
  margin-bottom: 28px;
  margin-top: 28px;
}

#section-7 .card {
  width: 280px;
  height: 350px;
  background: #ffffff;
  position: relative;
  transition: all 1s ease-in-out;
}

#section-7 .card:hover {
  background: #fafafa;
  box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.07);
  /*   height: 70px; */
}

#section-7 .img {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  display: block;
  z-index: 1 !important;
}

#section-7 .img-container {
  display: flex;
  justify-content: center;
  margin-top: 0px;
  z-index: 20 !important;
  overflow-x: visible !important;
  overflow-y: visible !important;
}

#section-7 .inner-container {
  margin-top: 20px;
}

#section-7 .card {
  margin-right: 20px;
  margin-left: 20px;
  padding: 20px 20px;
  display: flex;
  align-items: stretch;
}

#section-7 .card .title {
  font-size: 22px;
  color: #000000;
  font-weight: 700;
}

#section-7 .card .title-2 {
  color: #7e92ac;
  font-size: 14px;
}

#section-7 .card .rating {
  display: flex;
  justify-content: center;
  margin-top: 80px;
}

#section-8 {
  background: #4f29bf;
  padding: 40px 30px;
}

#section-8 .inner-container {
  display: flex;
  justify-content: center;
}

#section-8 .card {
  margin-right: 8px;
  margin-left: 8px;
  padding: 20px 20px;
  display: flex;
  align-items: stretch;
  width: 280px;
  height: 480px;
  transition: all 1s ease;
}

#section-8 .card:hover {
  background: #6837fa !important;
}

#section-8 .card:hover .price,
#section-8 .card:hover .sub-title,
#section-8 .card:hover .sub,
#section-8 .card:hover #offers {
  color: #ffffff;
}

#section-8 .card:hover button {
  color: #ffffff;
  background: #572bd9;
}

