#section-8 .header {
  color: #ffffff;
  text-align: center;
}

#section-8 .title {
  color: #ffffff;
  font-size: 14px;
  text-align: center;
  margin-bottom: 60px;
}

#section-8 .card .sub-title {
  color: #000000;
  font-size: 22px;
  text-align: center;
  transition: all 1s ease;
}

#section-8 .card .price {
  color: #000000;
  font-size: 35px;
  text-align: center;
  margin-top: 14px;
  font-weight: 700;
  transition: all 1s ease;
}

#section-8 .card .sub {
  color: #000000;
  font-size: 12px;
  text-align: center;
  transition: all 1s ease;
}

#section-8 .card button {
  background: #572bd9;
  color: #ffffff;
  display: inline-block;
}

#section-8 .card #offers {
  margin-top: 60px;
  margin-bottom: 40px;
  margin-left: 20px;
  margin-right: 20px;
}

#section-8 .card #offers img {
  margin-right: 10px;
}

#section-9 {
  background: #f9fbff;
  padding: 50px 70px;
}

#section-9 .header {
  color: #000000;
  text-align: center;
}

#section-9 .sub-header {
  font-size: 14px;
  color: #000000;
  font-weight: 500;
  text-align: center;
}

#section-9 .row {
  padding-top: 40px;
  /*   padding-bottom: 40px; */
  margin-left: 0 !important;
  margin-right: 0 !important;
}

#section-9 .title {
  color: #000000;
  font-weight: 700;
  font-size: 20px;
}

#section-9 .sub-title {
  color: #7e92ac;
  font-weight: 500;
  font-size: 14px;
}

#section-9 .col-md-6 {
  margin-bottom: 30px;
}

#section-10 {
  background: #ffffff;
  padding: 50px 70px;
}

#section-10 .header {
  color: #000000;
  text-align: center;
}

#section-10 form {
  background: #ffffff;
  margin-top: 50px;
  margin-left: auto;
  margin-right: auto;
  width: 400px;
}

#section-10 form button {
  margin: 0 auto;
  display: block;
}

#footer {
  padding: 50px 80px;
  background: #f9fbff;
}

#footer button {
  background: #572bd9;
  color: #ffffff;
}

#footer h4 {
  color: #000000;
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 20px;
}
#footer .icons img {
  margin-right: 15px;
}

#footer .row {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

#footer .sub {
  color: #7e92ac;
  font-size: 14px;
}

#footer ul {
  list-style-type: none !important;
  padding-left: 0 !important;
  color: #7e92ac;
  font-size: 14px;
}

#footer ul li {
  margin-bottom: 15px;
}

#footer #inner-container {
  padding-left: 40px;
}

#footer-2 {
  background: #5b2aee;
  padding: 20px 80px;
  color: #ffffff;
}

#footer-2 .row {
  margin-left: 0 !important;
  margin-right: 0 !important;
}

#footer-2 .d-flex {
  align-items: center;
}

#footer-2 .d-flex div {
  color: #f0f4fa;
  font-size: 14px;
  margin-right: 20px;
}

#footer-2 .copyright a {
  margin-left: 10px;
  color: #f0f0f0;
  font-weight: 600;
  font-size: 18px;
}

